import { Directive, ElementRef, OnInit, HostListener } from '@angular/core';
import { Snake } from '../classes/stake.class';
import { Game } from '../classes/game.class';
import { Fruit } from '../classes/fruit.class';

@Directive({
  selector: '[snake]'
})
export class SnakeDirective implements OnInit {
  private _score: number;
  private _block: number;
  private _keyBlock: boolean;

  private _snake: Snake;
  private _game: Game;
  private _fruit: Fruit;
  private _canvas: HTMLCanvasElement;
  private _context: CanvasRenderingContext2D;

  private _width: number;
  private _height: number;

  @HostListener('document:keydown', ['$event']) onKeyPress(event: KeyboardEvent) {
    this.keyPressListener(event);
  }

  constructor(private _elementRef: ElementRef) {
    this._canvas = this._elementRef.nativeElement;
    this._context = this._canvas.getContext('2d');

    this._width = this._canvas.width;
    this._height = this._canvas.height;
  }

  ngOnInit(): void {
    this._score = 0;

    this._snake = new Snake(this._width, this._height);
    this._game = new Game();
    this._fruit = new Fruit(this._width, this._height, this._snake.failCheck.bind(this._snake));

    this._game.gameStep.subscribe((eventObject: {}) => this.updateElement(eventObject));
    this.startGame();
  }

  private keyPressListener(event: KeyboardEvent) {
    event.preventDefault();
    console.log(event);

    switch (event.keyCode) {
      case 38: case 87: if (this._block != 1 && !this._keyBlock) {
        this._snake.motionVectorX = 0;
        this._snake.motionVectorY = -1;
        this._block = 1;
        this._keyBlock = true;
      }; break;
      case 39: case 68: if (this._block != 2 && !this._keyBlock) {
        this._snake.motionVectorX = 1;
        this._snake.motionVectorY = 0;
        this._block = 2;
        this._keyBlock = true;
      }; break;
      case 40: case 83: if (this._block != 1 && !this._keyBlock) {
        this._snake.motionVectorX = 0;
        this._snake.motionVectorY = 1;
        this._block = 1;
        this._keyBlock = true;
      }; break;
      case 37: case 65: if (this._block != 2 && !this._keyBlock) {
        this._snake.motionVectorX = -1;
        this._snake.motionVectorY = 0;
        this._block = 2;
        this._keyBlock = true;
      }; break;
    }
  }

  private startGame() {
    this._fruit.generateFruit();
    this._game.start();
  }

  private updateElement(eventObject: {}) {
    this._keyBlock = false;

    this.updateFill();
    this.generateFruit();

    this._snake.updateSnakePosition();

    if (this._snake.checkCollision()) {
      this.gameFail();
      this.startGame();
      return;
    } else {
      this._snake.snakeStape();
    }

    this.snakeFill();
    if (this._snake.snakeXPosition === this._fruit.fruitXPosition && this._snake.snakeYPosition === this._fruit.fruitYPosition) {
      this.scoreUp();
    } else {
      this._snake.snakeNoResize();
    }
  }

  private scoreUp() {
    this._score++;
    this.updateFill();
    this._fruit.generateFruit();
    this.generateFruit();
    this.snakeFill();
  }

  private updateFill() {
    this._context.fillStyle = 'SpringGreen';
    this._context.fillRect(0, 0, this._width, this._height);

    this._context.shadowBlur = 2;
    this._context.shadowColor = 'MidnightBlue';
  }

  private generateFruit() {
    let fruitSize = this._fruit.fruitSize,
      fruitXPosition = this._fruit.fruitXPosition,
      fruitYPosition = this._fruit.fruitYPosition;

    this._context.beginPath();
    this._context.arc(fruitXPosition + fruitSize / 2, fruitYPosition + fruitSize / 2, fruitSize / 1.6, fruitSize / 1.6, Math.PI * 2, true)
    this._context.fillStyle = 'Red';
    this._context.fill();
    this._context.strokeStyle = 'Lime';
    this._context.stroke();

    this.updateScore();
  }

  private snakeFill() {
    let snakeSize = this._snake.snakeSize,
      snakeXPositions = this._snake.snakeXPositions,
      snakeYPositions = this._snake.snakeYPositions;

    this._context.fillStyle = 'Blue';
    for (let index = 0; index <= snakeXPositions.length; index++) {
      this._context.fillRect(snakeXPositions[index] + 1, snakeYPositions[index] + 1, snakeSize - 2, snakeSize - 2);
    }
  }

  private gameFail() {
    this._game.stop();
    this._score = 0;

    this._snake.resetSnake();

    this._context.fillStyle = 'White';
    this.updateFill();
  }

  private updateScore() {
    let fruitSize = this._fruit.fruitSize,
      fruitXPosition = this._fruit.fruitXPosition,
      fruitYPosition = this._fruit.fruitYPosition;

    this._context.fillStyle = 'Black';
    this._context.font = 'bold 13pt Arial';
    this._context.textBaseline = 'middle';
    this._context.textAlign = 'center';
    this._context.fillText(`${this._score + 1}`, fruitXPosition + fruitSize / 2, fruitYPosition + fruitSize / 2);
  }
}
