export class Fruit {
  private FRUIT_SIZE: number = 25;

  private _currentFruitXPosition: number;
  private _currentFruitYPosition: number;

  get fruitSize(): number {
    return this.FRUIT_SIZE;
  }

  get fruitXPosition(): number {
    return this._currentFruitXPosition;
  }

  get fruitYPosition(): number {
    return this._currentFruitYPosition;
  }

  public constructor(private _width: number, private _height: number,
    private _failCheck: (x: number, y: number) => boolean) { }

  public generateFruit() {
    this._currentFruitXPosition = this.generateCoordinate(this._width);
    this._currentFruitYPosition = this.generateCoordinate(this._height);

    if (this._failCheck(this._currentFruitXPosition, this._currentFruitYPosition)) this.generateFruit();
  }

  private generateCoordinate(value: number): number {
    return Math.round((value / this.FRUIT_SIZE - 1) * Math.random()) * this.FRUIT_SIZE;
  }
}
