import { Output, EventEmitter } from '@angular/core';

export class Game {
  private _gameProcessTimer: any;
  private _delay: number;

  @Output() gameStep = new EventEmitter<{}>();

  public constructor() {
    this.initilize();
  }

  public start() {
    this._gameProcessTimer = setInterval(this.eventEmit.bind(this), this._delay);
  }

  public stop() {
    clearInterval(this._gameProcessTimer);
  }

  private initilize(): void {
    this._delay = 1000 / 5;
  }

  private eventEmit(): void {
    this.gameStep.emit({});
  }
}
